export default {
	components:{
		
	},
	data() {
		return {
			user:null,
			loginUser: "",
			msg:""
		}
	},
	methods: {
		search(args){
			this.$http.user.searchUser(args.value).then(res => {
				if(res.data === null){
					uni.showToast({
						title:"未找到用户",
						icon:"none"
					})
				}else{
					this.loginUser = this.$store.state.loginUser.nickname;
					if(this.loginUser === 'undefined'){
						this.$global.restoreUserState();
						this.loginUser = this.$store.state.loginUser.nickname;
					}
					
					this.user = res.data;
					this.msg = '你好，我是'+this.loginUser;
				}
					
			}).catch(err => console.log(err));
		},
		sendFriendRequest(){
			this.$http.friend.sendFriendRequest({msg:this.msg,acceptUserId:this.user.id}).then(res =>{
				if(res.code === "200"){
					uni.showToast({
						title:"发送成功",
						icon:"none"
					});
					uni.navigateBack({
						delta:-1
					})
				}else{
					uni.showToast({
						title:res.message,
						icon:"none"
					})
				}
					
			}).catch(err => console.log(err));
		}
	},
	
	
}
